<template>
	<view class="pathway on-pathway" @touchend="onEnd">
		<view class="tips">
			<text v-if="isOk" v-show='timestatus' style="color: #FFFFFF;">{{countdown}}秒重新获取验证码</text>
			<text v-else>滑动获取验证码</text>
		</view>
		<view class="track" :style="{'transform':'translateX('+oldx+'px)'}"></view>
		<movable-area :animation="true">
			<movable-view class="on-track" :class="{'active':isOk}" 
				:x="x" direction="horizontal" @change="onMove"
				:disabled="isOk"
			></movable-view>
		</movable-area>
	</view>
</template>

<script>
	export default {
		props: ["mobile"],
		name: 'move-verify',
		data() {
			return {
				x: 0,
				oldx:0,
				isOk: false,
				size: {},
				count:0,
				isMove:false,
				countdown: '',
				timestatus: true,
				phoneExistance: true
			};
		},
		mounted() {
			let getSize = (selector) => {
				return new Promise((resolve, reject) => {
					let view = uni.createSelectorQuery().in(this).select(selector);
					view.fields({
						size: true,
					}, (res) => {
						resolve(res.width);
					}).exec();
				});
			}
			this.$nextTick(() => {
				getSize(".on-pathway").then((res1) => {
					this.size.pathway = res1;
					getSize(".on-track").then((res2) => {
						this.size.track = res2;
					});
				})
			})
		},
		methods: {
			/* 滑动中 */
			onMove(e) {
				this.oldx = e.detail.x;
			},
			/* 滑动结束 */
			onEnd() {
				if (this.isOk || this.oldx<1) {
					return;
				}
				
				this.count++;
				this.x = this.oldx;
				if ((this.oldx + 3) > (this.size.pathway - this.size.track)) {
				
					this.isOk = true;
					this.$emit("result",{flag:true,count:this.count});
				
					this.checkPhoneExistance();
					this.countDown();
					
				} else {
					this.$nextTick(() => {
						setTimeout(() => {
							this.x = 0;
							this.oldx = 0;
						}, 100);
					});
					this.$emit("result",{flag:false,count:this.count});
				}
			},
			/* 重置 */
			reset(){
				this.x = 0;
				this.oldx = 0;
				this.count = 0;
				this.isOk = false;
			},
			/* 倒计时 */
			countDown(){
				//倒计时
				this.timestatus = true
				this.countdown = 60
				var interval = setInterval(() => {
					let times = --this.countdown
					this.countdown = times <= '0' ? + times : times
				}, 1000)
				//设置超时
				setTimeout(() => {
					clearInterval(interval)
					this.countdown = ''
					this.timestatus = false;
					this.x = 0;
					this.oldx = 0;
					this.isOk = false;
				}, 60000)
			},
			checkPhoneExistance(){
				uni.request({
					url: this.apiServer+'/customer/checkPhoneExistance',
					method: 'POST',
					dataType: "json",
					data: { 
					   "mobile": this.mobile,
					},
					success: (res) => {
						const result = res.data
						console.log(result)
						if(result != 0){
							this.phoneExistance = true;
						}else{
							this.phoneExistance = false;
						}
				    }
				});
			}
			
		}
	}
</script>

<style scoped lang="scss">
	.pathway {
		height: 80rpx;
		width: 100%;
		margin-bottom: 18px;
		background-color: #7ac23c;
		position: relative;
		overflow: hidden;
		
		/* 提示信息 */
		.tips {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			line-height: 80rpx;
			text-align: center;
			color: #666;
			font-size: 32rpx;
			z-index: 3;
		}
		
		/* 滑动轨道 */
		.track {
			position: absolute;
			top: 0;
			left: 0;
			background-color: #eeeeee;
			width: 100%;
			height: 100%;
			padding-left: 0;
			box-sizing: content-box;
			transform: translateX(0);
		}
		
		movable-area {
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			background: none;
			z-index: 5;
		}
		
		movable-view {
			height: 100%;
			width: 100rpx;
			box-sizing: border-box;
			background-color: #fff;
			border: #dddddd solid 1px;
			background-position: center;
			background-repeat: no-repeat;
			background-size: auto 32rpx;
			background-image: url("data:image/png;base64,");
		}
		
		movable-view.active {
			border: #7ac23c solid 1px;
			background-image: url("data:image/png;base64,");
		}
		
		/* 禁止 */
		.disabled {
			position: absolute;
			z-index: 10;
			height: 100%;
			width: 100%;
			top: 0;
			left: 0;
		}
	}
</style>